  </div>
</template>
<script>

export default {
  name: 'keyboard',
  props: [
    'city'
  ],
  data () {
    return {
      currentStr: ''
    }
  },
  methods: {
    inputNumber (value) {
      this.currentStr += value
    },
    inputPrefix (value) {
      this.currentStr = ''
      this.currentStr += value
    },
    clearInput () {
      this.currentStr = ''
    },
    showMoreArea () {
      document.querySelector('.keyboard-moreArea').classList.toggle('active')
    }
  },
  watch: {
    currentStr () {
      // console.log(this.currentStr)
      this.$emit('emit-input', this.currentStr)
    }
  },
  mounted () {
    console.log(this.city)
  },
  created () {
  }
}
</script>
